.past__round {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45px;
    height: 30px;
    object-fit: contain;
    opacity: 0.9;
    border: solid 1px rgba(115, 115, 115, 0.5);
    border-radius: 45%;
    font-size: 18px;
    font-weight: 800;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f1f1f1;
}

.past__round_black {
    background-image: radial-gradient(circle at 50% 100%, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.08) 88%),
        linear-gradient(to bottom, #000, #000000);
}
.past__round_green {
    background-image: radial-gradient(circle at 50% 100%, #14ff00, rgba(20, 255, 0, 0.25) 93%),
        linear-gradient(to bottom, rgba(20, 255, 0, 0.2), rgba(20, 255, 0, 0.2));
}
.past__round__red {
    background-image: radial-gradient(circle at 50% 100%, #f00, rgba(255, 0, 0, 0.25) 93%),
        linear-gradient(to bottom, rgba(255, 0, 0, 0.2), rgba(255, 0, 0, 0.2));
}
