.skin {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 84px;
    height: 105px;
    box-sizing: border-box;
    background-color: rgba(131, 128, 154, 0.2);
    border-top: #2bbfff solid 2px;
    border-radius: 4px 4px 2px 2px;
    padding: 6px 2px 4px;
    cursor: pointer;
}
.skin:hover {
    border-top: #2bd8ff solid 2px;
}
.skin::after {
    content: '';
    width: 66px;
    height: 4px;
    opacity: 1;
    -webkit-filter: blur(10px);
    filter: blur(10px);
    background-color: #2bc9ff;
}
.skin_selected {
    border: rgba(43, 201, 255, 0.81) solid 1px;
    border-top: #2bc9ff solid 2px;
}
.skin__background {
    position: absolute;
    bottom: 0;
    opacity: 0.6;
    -webkit-filter: blur(10px);
    filter: blur(10px);
    background-color: #2bc9ff;
}
.skin__img {
    width: 68px;
    height: 68px;
    object-fit: cover;
    opacity: 0.6;
}
.skin__price {
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #b8b8b8;
}
