.profile-accordion-content {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    column-gap: 40px;
    row-gap: 45px;
    padding: 5px 40px 48px;
    background-color: rgba(131, 128, 154, 0.15);
}
.profile-accordion-content__container-input {
    display: flex;
    flex-direction: column;
}
.profile-accordion-content__label {
    width: 240px;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.profile-accordion-content__input {
    width: 100%;
    height: 44px;
    margin: 12px 0 0;
    padding: 12px 7px 13px 12px;
    border-radius: 6px;
    border: solid 1px #585a5c;
    background-color: #2c3136;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #2f2828;
}

.profile-accordion-content__input:focus {
    outline: rgba(124, 221, 255, 0.39);
    border: solid 1px #7cddff63;
    background-color: #1d2024;
    color: #e7e7e7;
}

.profile-accordion-content__country-select {
    width: 240px;
    height: 44px;
    margin: 12px 0 0;
    padding: 12px 12px 12px 12px;
    border-radius: 6px;
    border: solid 1px #585a5c;
    background-color: #2c3136;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #989696;
    /*width: 100%;*/
    max-height: 200px;
    overflow-y: scroll;
}
.profile-accordion-content__country-option {
    /* Стили для опций выпадающего списка стран */
}

.profile-accordion-content__country-flag {
    /* Стили для флагов стран */
}

.profile-accordion-content__input-text {
    font-size: 13px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: var(--text-white);
}
.profile-accordion-content__label_trade-link {
    width: 100%;
    max-width: 520px;
}
.profile-accordion-content__checkbox-container1 {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

