.profile-accordion-content__button {
    width: 196px;
    height: 44px;
    padding: 12px 0 13px 1px;
    border-radius: 9px;
    box-shadow: inset 0 0 15px 0 rgba(0, 181, 244, 0.69);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(to bottom, #00b5f4, rgba(0, 181, 244, 0.3) 99%);
    border-image-slice: 1;
    background-image: radial-gradient(circle at 50% 100%, #00b5f4, rgba(0, 181, 244, 0.25) 41%), linear-gradient(to bottom, #003a4e, #003a4e),
        linear-gradient(to bottom, #00b5f4, rgba(0, 181, 244, 0.3) 99%);
    background-origin: border-box;
    /*background-clip: content-box, content-box, border-box;*/
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f1f1f1;
    cursor: pointer;
}

.profile-accordion-content--second {
    display: flex;
    flex-direction: column;
}
.profile-accordion-content--third {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}
.profile-accordion-content--fourth {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.profile-accordion-content__title {
    /* Стили для заголовков блоков аккордеона */
}

.profile-accordion-content--default .profile-accordion-content__text {
    /* Стили для текста в блоке по умолчанию */
}
.profile-accordion-content__text {
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: var(--text-gray-2);
}
.profile-accordion-content--fourth-button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 170px;
    height: 30px;
    opacity: 0.9;
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.2);
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: var(--text-gray-2);
    cursor: pointer;
}

/* Модификаторы (по желанию) */
.profile-accordion-content__text--centered {
    /* Стили для текста с выравниванием по центру */
}
.profile-accordion-content__create-promo-codes {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 10px;
}

.profile-accordion-content__info-block {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    justify-content: center;
    align-items: flex-start;
    width: 168px;
    height: 56px;
    padding: 9px 18px;
    gap: 3px;
    border-radius: 6px;
    background-color: rgba(131, 128, 154, 0.15);
}

.profile-accordion-content__block-title {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.profile-accordion-content__block-info {
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #f1f1f1;
}
.profile-accordion-content__promo-codes {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(6, minmax(min-content, 1fr));
    grid-template-rows: repeat(auto-fit, minmax(44px, 1fr));
}
.profile-accordion-content__promo-codes-title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: var(--text-gray-1);
    box-sizing: border-box;
    padding: 0 0 0 10px;
}
.profile-accordion-content__promo-code {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 44px;
    background-color: rgba(131, 128, 154, 0.15);
    margin-bottom: 2px;
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: var(--text-gray-2);
    padding: 0 0 0 10px;
}
