.big-button {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 52px;
    height: 44px;
    max-width: 200px;
    padding: 10px 16px;
    border-radius: 6px;
    border: none;
    cursor: pointer;
}

.big-button__icon {
    padding-right: 8px;
}

.big-button__text {
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    transition: 0.3s;
}

.big-button__text_grey {
    color: #7e8487;
}

.big-button__text_white {
    color: #f1f1f1;
}

.big-button_background_grey {
    background-color: rgba(131, 128, 154, 0.2);
    background-image: linear-gradient(to bottom, rgba(32, 49, 55, 0.46), rgba(32, 49, 55, 0.46));
}

.big-button_background_blue {
    background-color: #00b5f4;
    background-image: none;
}

.big-button_background_blue-blur {
    box-shadow: inset 0 0 15px 0 rgba(0, 181, 244, 0.69);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(to bottom, #00b5f4, rgba(0, 181, 244, 0.3) 99%);
    border-image-slice: 1;
    background-image: radial-gradient(circle at 50% 100%, #00b5f4, rgba(0, 181, 244, 0.25) 41%), linear-gradient(to bottom, #003a4e, #003a4e),
        linear-gradient(to bottom, #00b5f4, rgba(0, 181, 244, 0.3) 99%);
    /*transition: 0.5s ease-in-out;*/
    transition: 0.2s ease-in-out;
    opacity: 0.8;
}

.big-button_background_blue-blur:hover {
    box-shadow: inset 0 0 15px 0 rgba(160, 230, 255, 0.69);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(to bottom, #00b5f4, rgba(0, 181, 244, 0.3) 99%);
    border-image-slice: 1;
    background-image: radial-gradient(circle at 50% 100%, #00b5f4, rgba(0, 181, 244, 0.48) 42%), linear-gradient(to bottom, #006385, #006385),
        linear-gradient(to bottom, #00b5f4, rgba(0, 181, 244, 0.3) 99%);
    transition: 0.2s ease-in-out;
    opacity: 1;
}
