.double {
    display: flex;
    justify-items: start;
    flex-direction: column;
    width: 100%;
    border-radius: 6px;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.double__background-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 120px;
    height: 120px;
    object-fit: contain;
}
.double__up-container {
    display: flex;
    justify-content: center;
    gap: 5%;
    align-items: center;
    min-height: 430px;
    width: 100%;
    box-sizing: border-box;
    padding: 0 0 0 0;
}
.double__wheel-container {
    position: relative;
    /*width: 50%;*/
    /*height: 50%;*/
    /*max-width: 388px;*/
    /*max-height: 388px;*/
    box-sizing: border-box;
}
.double__animation-container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 33%;
    height: 33%;
    box-sizing: border-box;
    z-index: 0;
}
.double__animation-number {
    position: absolute;
    top: 5%;
    bottom: 0;
    left: 35%;
    right: 0;
    margin: auto;
    background-image: linear-gradient(to bottom, #959595 13%, #fff 54%, #e5dede 55%, #d8d8d8 89%);
    font-size: 75px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}
.double__text {
    position: absolute;
    top: 70%;
    bottom: 0;
    left: 25%;
    right: 0;
    margin: auto;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}

.double__arrow {
    position: absolute;
    right: 0px;
    top: 35px;
    bottom: 0;
    margin: auto;
    z-index: 3;
}
.double__wheel {
    width: 100%;
    height: 100%;
    max-width: 388px;
    max-height: 388px;
    object-fit: contain;
    z-index: 2;
}
.double__form-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 46%;
    height: 280px;
    max-width: 580px;
    padding: 20px 0 0 0;
    box-sizing: border-box;
}
.double__form {
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    box-sizing: border-box;
    margin: 0 0 20px 0;
}
.double__label {
    position: absolute;
    top: -24px;
    width: 100%;
    max-width: 240px;
    height: 17px;
    margin: 0;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
}

.double__input {
    width: 10%;
    max-width: 240px;
    height: 56px;
    margin: 0 20px 0 0;
    padding: 15px 174px 14px 20px;
    object-fit: contain;
    opacity: 0.9;
    box-sizing: border-box;
    border-radius: 10px;
    border: solid 1px rgba(115, 115, 115, 0.7);
    background-color: rgba(0, 0, 0, 0.2);
}
.double__form-button {
    width: 120px;
    height: 56px;
    object-fit: contain;
    border-radius: 3px;
    border-style: solid;
    border-width: 1px;
    box-sizing: border-box;
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f1f1f1;
}
.double__form-button_left {
    width: 120px;
    height: 56px;
    object-fit: contain;
    border-radius: 10px;
    box-shadow: inset 0 0 15px 0 rgba(255, 140, 0, 0.69);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(to bottom, #ff7474, rgba(255, 145, 145, 0.3));
    border-image-slice: 1;
    background-image: radial-gradient(circle at 50% 84%, #f00, rgba(255, 0, 0, 0.25) 74%),
        linear-gradient(to bottom, rgba(255, 0, 0, 0.48), rgba(255, 0, 0, 0.5)), linear-gradient(to bottom, #ff7474, rgba(255, 145, 145, 0.3));
    background-origin: border-box;
    background-clip: content-box, content-box, border-box;
}
.double__form-button_center {
    width: 120px;
    height: 56px;
    object-fit: contain;
    opacity: 0.9;
    border: solid 1px rgba(20, 255, 0, 0.7);
    background-image: radial-gradient(circle at 50% 100%, #14ff00, rgba(20, 255, 0, 0.25) 71%),
        linear-gradient(to bottom, rgba(20, 255, 0, 0.2), rgba(20, 255, 0, 0.2));
}
.double__form-button_right {
    width: 120px;
    height: 56px;
    object-fit: contain;
    opacity: 0.9;
    border-radius: 10px;
    border: solid 1px rgba(115, 115, 115, 0.7);
    background-image: radial-gradient(circle at 50% 100%, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.08) 67%),
        linear-gradient(to bottom, #000, #000);
}
.double__container-button {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 9px;
    margin: 0 0 94px 0;
    width: 100%;
}
.double__rate-button {
    max-width: 53px;
    border-radius: 6px;
    border: none;
    background-color: rgba(255, 255, 255, 0.1);
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #7e8487;
    box-sizing: border-box;
}
.double__past-rounds {
    position: relative;
    width: 100%;
    max-width: 580px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    gap: 5%;
    flex-wrap: nowrap;
    box-sizing: border-box;
    z-index: 3;
}
.double__past-rounds-info {
    position: absolute;
    top: -50px;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
    width: 300px;
    gap: 5px;
}
.double__past-number-rounds {
    opacity: 0.3;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #fff;
}
.double__down-container {
    display: flex;
    justify-content: center;
    gap: 24px;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
}
