.bank__container {
    position: relative;
    width: 35%;
    flex-wrap: nowrap;
    gap: 13px;
    padding: 0 24px 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.bank__container:first-child {
    align-items: flex-start;
}
.bank__container:last-child {
    align-items: flex-end;
}
.bank__money-container {
    display: flex;
}
.bank__money-img {
    width: 52px;
    height: 52px;
    margin: 0 8px 4px 0;
    object-fit: contain;
}
.bank__money-text {
}

.bank__balance {
    font-size: 40px;
    font-weight: 800;
    color: #f1f1f1;
}
.bank__balance-rub {
    font-size: 20px;
}
