.market {
    width: 100%;
}

.market__header {
    display: flex;
    align-items: start;
    justify-content: center;
    width: 100%;
    height: 51px;
    border-bottom: 1px solid #4e4d57;
}

.market__button-position {
    margin-right: 5px;
}

.market__balance {
    padding: 25px 24px;
    display: flex;
    gap: 24px;
}

.market__balance-text {
    font-family: Manrope, serif;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}

.market__balance-text_bold {
    font-size: 14px;
    font-weight: bold;
    text-align: right;
    color: #f1f1f1;
}
