.block-side-bar-list {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 56px 10px 10px 16px;
    width: 100%;
    max-width: 280px;
    min-height: 124px;
    /*margin: 16px 0;*/
    border-radius: 6px;
    background-image: linear-gradient(to bottom, rgba(49, 64, 68, 0.7), rgba(36, 36, 36, 0.7));
    box-sizing: border-box;
}

.block-side-bar-list__header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    max-width: 280px;
    height: 36px;
    object-fit: contain;
    /*margin: 0 0 16px;*/
    /*padding: 8px 130px 9px 16px;*/
    background-image: url('../../../images/BlockSideBar.png');
}
.block-side-bar-list__title {
    position: absolute;
    top: 8px;
    left: 16px;
    width: 139px;
    height: 19px;
    background-image: linear-gradient(to bottom, #959595 13%, #fff 54%, #e5dede 55%, #d8d8d8 89%);
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0;
    padding: 0;
}
