.profile {
    position: relative;
    width: 100%;
    min-height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    box-sizing: border-box;
}
.profile__background-text-container {
    position: absolute;
    display: flex;
    gap: 3px;
    flex-direction: column;
    top: 24px;
    left: 24px;
}
.profile__background-text {
    /*top: 29px;*/
    /*left: 24px;*/
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.profile__like-container {
    position: absolute;
    top: 16px;
    right: 16px;
    display: flex;
    justify-content: space-between;
    width: 75px;
    height: 44px;
    padding: 13px 15px;
    border-radius: 10px;
    background-color: rgba(23, 22, 26, 0.5);
    box-sizing: border-box;
    border: none;
    cursor: pointer;
}
.profile__like-text {
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.profile__social-container {
    position: absolute;
    top: 90px;
    left: 20px;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    cursor: pointer;
}
.profile__summary {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    min-height: 41vh;
    padding: 72px 0 0 0;
    background-image: url('../../images/backgroundProfile.webp');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
}
.profile__avatar-container {
    border: 3px solid rgba(33, 31, 31, 0.83);
    border-radius: 10px;
    box-sizing: border-box;
    margin: 0 0 14px 0;
}
.profile__avatar-name {
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f1f1f1;
}
.profile__avatar-name-last-child {
    margin: 0 0 4px 0;
}
.profile__avatar-band {
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
    margin: 0 0 8px 0;
}
.profile__avatar-rating {
    font-size: 12px;
    margin: 0 0 32px 0;
}
.profile__avatar-history {
    display: flex;
    justify-content: center;
    width: 100%;
    gap: 6px;
}
.profile__avatar-history-block {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 170px;
    height: 64px;
    border-radius: 6px;
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
    border: solid 1px #606060;
    background-image: radial-gradient(circle at 50% 100%, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.08) 61%),
        linear-gradient(to bottom, rgba(41, 49, 52, 0.7), rgba(41, 49, 52, 0.7));
}
.profile__avatar-ico-container {
    position: absolute;
    left: 15px;
}
.profile__title {
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #fff;
}
.profile__text {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.profile__level {
    width: 100%;
    overflow: hidden;
    height: 110px;
    padding: 10px 0;
    box-sizing: border-box;
    background-color: #2b3134;
}

.profile__down-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 4px;
    padding: 0 12px;
    box-sizing: border-box;
    background-color: #2b3134;
}

.profile__settings {
    position: relative;
    display: flex;
    width: 100%;
    min-height: 50px;
    padding: 40px 13px 16px;
    box-sizing: border-box;
    align-items: flex-start;
    flex-direction: column;
    gap: 8px;
    justify-content: flex-start;
    background-color: rgba(131, 128, 154, 0.15);
    cursor: pointer;
}
.profile__settings-item {
    position: absolute;
    left: 13px;
    top: 16px;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
    transition: max-height 0.4s ease-out;
}
.profile__accordion {
    overflow: hidden;
    transition: max-height 0.3s ease;
    max-height: 100%;
}

.profile__accordion.open {
    transform: scaleY(1);
}

.profile__accordion-content {
    box-sizing: border-box;
    transform-origin: top;
    backface-visibility: hidden; /* Это свойство помогает предотвратить проблемы с производительностью анимации */
}
