.checkbox:not(checked) {
    position: absolute;
    z-index: -1;
    opacity: 0;
}
.checkbox:not(checked) + .checkbox__label-text {
    position: relative;
    padding: 0 0 0 51px;
    cursor: pointer;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
    transition: 0.3s ease-in-out;
}
.checkbox:not(checked) + .checkbox__label-text:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    transition: all 0.3s cubic-bezier(0.25, 0.1, 0.25, 1);
    width: 40px;
    height: 24px;
    border-radius: 6px;
    background-color: rgba(255, 255, 255, 0.1);
}
.checkbox:not(checked) + .checkbox__label-text:after {
    content: '';
    position: absolute;
    top: 4px;
    left: 3px;
    transition: all 0.3s cubic-bezier(0.7, -0.1, 0.25, 1.1);
    width: 16px;
    height: 17px;
    opacity: 0.8;
    border-radius: 4px;
    background-color: #7e8487;
}
.checkbox:checked + .checkbox__label-text:before {
    background: #00b5f4;
}
.checkbox:checked + .checkbox__label-text:after {
    left: 20px;
    background: rgb(63, 63, 63);
}
.checkbox__label-text:hover {
    transform: scale(1.05);
    opacity: 80%;
}

