.classic__start {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    padding: 15px 20px 20px;
}
.classic__down-container {
    width: 100%;
    min-height: 33vh;
    max-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.classic__img {
    max-width: 120px;
    max-height: 120px;
}
.classic__text {
    font-size: 40px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
}
