.in-bank {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    box-sizing: border-box;
    margin: 25px 0 0 0;
}
.in-bank__container {
    position: relative;
    width: 35%;
    flex-wrap: nowrap;
    gap: 13px;
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.in-bank__container:first-child {
    align-items: flex-start;
}
.in-bank__container:last-child {
    align-items: flex-end;
}
.in-bank__players-svg {
    margin: 0 8px 0 0;
    padding: 0;
    opacity: 0.6;
}
.in-bank__put-text {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.in-bank__players {
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #f1f1f1;
}
.in-bank__players-text {
}
.in-bank__money-container {
    display: flex;
}
.in-bank__money-img {
    width: 52px;
    height: 52px;
    margin: 0 8px 4px 0;
    object-fit: contain;
}
.in-bank__money-text {
}

.in-bank__balance {
    font-size: 40px;
    font-weight: 800;
    color: #f1f1f1;
}
.in-bank__balance-rub {
    font-size: 20px;
}
.in-bank__button-skins {
    width: 200px;
    height: 56px;
    padding: 15px 0 14px;
    border-radius: 10px;
    box-shadow: inset 0 0 15px 0 rgba(255, 140, 0, 0.69);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(to bottom, #ff7474, rgba(255, 145, 145, 0.3));
    border-image-slice: 1;
    background-image: radial-gradient(circle at 50% 84%, #f00, rgba(255, 0, 0, 0.25) 47%),
        linear-gradient(to bottom, rgba(255, 0, 0, 0.48), rgba(255, 0, 0, 0.5)), linear-gradient(to bottom, #ff7474, rgba(255, 145, 145, 0.3));
    background-origin: border-box;
    background-clip: content-box, content-box, border-box;
    cursor: pointer;
}
.in-bank__auto-past {
    display: flex;
    justify-content: center;
    gap: 3px;
    align-items: center;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
    margin: 0 47px 0 0;
}
