.not-found {
    height: 100vh;
    width: 100vw;
    /*max-width: 1280px;*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;

    border-radius: 6px;
    background-image: linear-gradient(to bottom, rgba(49, 64, 68, 0.7), rgba(22, 22, 22, 0.7) 99%);
    margin: 0;
    padding: 0;
}
.not-found__container {
    display: flex;
    max-height: 30vh;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.not-found__title {
    width: 100%;
    max-width: 400px;
    text-align: center;
    font-weight: 400;
    font-size: 140px;
    line-height: 1.2;
    color: rgba(232, 232, 232, 0.52);
    margin: 0 0 10px;
}
.not-found__text {
    display: block;
    width: 100%;
    max-width: 400px;
    text-align: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    color: rgb(184, 184, 184);
    margin: 0 0 185px 0;
}
.not-found__back {
    max-width: 42px;
    background: none;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.2;
    color: rgba(232, 232, 232, 0.29);
    border: none;
    cursor: pointer;
    outline: none;
    padding: 0;
    margin: 0;
    bottom: 0;
}
.not-found__back:hover {
    opacity: 0.8;
}

@media screen and (max-width: 450px) {
    .not-found {
        height: 100vh;
        padding: 0;
        box-sizing: border-box;
        justify-content: center;
    }
    .not-found__container {
        display: flex;
        flex-direction: column;
        margin: 0;
        padding: 0;
    }
    .not-found__title {
        font-size: 80px;
        margin: 0 0 10px 0;
    }
    .not-found__text {
        font-size: 12px;
        margin: 0 0 284px 0;
    }
    .not-found__back {
        /*justify-self: end;*/
        font-size: 12px;
        margin: 0;
        padding: 0;
    }
}
