.inventory_open {
    position: relative;
    display: flex;
    background-color: rgba(0, 0, 0, 0.07);
    margin: auto;
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    min-height: 500px;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 60px 12px 0 12px;
    box-sizing: border-box;
    border-radius: 6px;
    background-image: linear-gradient(to bottom, rgba(49, 64, 68, 0.38), rgba(22, 22, 22, 0.47) 99%);
}
.inventory__header {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    height: 36px;
    padding: 0 0 0 16px;
    border-radius: 6px;
    box-sizing: border-box;
    z-index: 1;
    background-image: radial-gradient(circle at 50% 100%, rgba(49, 64, 68, 0.7), rgba(36, 36, 36, 0.85) 24%);
}
