.inventory__text {
    background-image: linear-gradient(to bottom, #959595 13%, #fff 54%, #e5dede 55%, #d8d8d8 89%);
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}
.inventory__close-button {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    left: -40px;
    top: 0;
    width: 32px;
    height: 32px;
    border-radius: 6px;
    box-sizing: border-box;
    background-color: #00769e;
    transition: 0.2s;
    cursor: pointer;
    border: none;
    z-index: 5;
}
.inventory__close-button:hover {
    transform: scale(1.05);
    opacity: 0.7;
}
.inventory__close-button:active {
    transform: scale(0.99);
    opacity: 1;
}
.inventory__sort {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    box-sizing: border-box;
    margin: 0 0 9px 0;
}
.inventory__sort-text {
    white-space: nowrap;
    flex-wrap: nowrap;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.inventory__sort-button-container {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    flex-wrap: wrap;
    padding: 9px 0 0;
    margin: 0 0 20px 0;
}
.inventory__button-highlight {
    width: 32px;
    height: 24px;
    margin: 0 5px 4px 0;
    padding: 4px 0 3px;
    border-radius: 6px;
    border: none;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.1);
}
.inventory__button-highlight:last-child {
    margin: 0 0 4px auto;
}
.inventory__button-highlight:first-child {
    /*margin: 9px 0 0 0;*/
}
.inventory__sort-info-container {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    flex-wrap: nowrap;
    gap: 5px;
    align-items: center;
}
.inventory__sort-text_first {
    width: 35%;
    justify-self: flex-start;
}
.inventory__container {
    position: relative;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 4px;
    box-sizing: border-box;
}

.inventory__info-container {
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    height: 145px;
    padding: 20px 20px 0 16px;
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    box-sizing: border-box;
    box-shadow: inset 0 1px 0 0 rgba(96, 95, 104, 0.7);
    background-color: rgba(30, 34, 37, 0.65);
}
.inventory__info-container-items {
    display: flex;
    flex-direction: column;
    gap: 4px;
    height: 40px;
}
.inventory__info-container-items:last-child {
    height: 40px;
    margin: 1px 20px 0;
    border-left: solid 1px #605f68;
    padding: 0 0 0 20px;
}
.inventory__info-container-text {
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.inventory__info-container-total {
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #f1f1f1;
}
.inventory__button-skin {
    position: absolute;
    bottom: 0;
    width: 232px;
    height: 44px;
    margin: 20px 12px 20px 16px;
    border-radius: 10px;
    box-shadow: inset 0 0 15px 0 rgba(255, 140, 0, 0.69);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(to bottom, #ff7474, rgba(255, 145, 145, 0.3));
    border-image-slice: 1;
    background-image: radial-gradient(circle at 50% 84%, #f00, rgba(255, 0, 0, 0.25) 36%),
        linear-gradient(to bottom, rgba(255, 0, 0, 0.48), rgba(255, 0, 0, 0.5)), linear-gradient(to bottom, #ff7474, rgba(255, 145, 145, 0.3));
    background-origin: border-box;
    background-clip: content-box, content-box, border-box;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f1f1f1;
}

@media screen and (max-width: 1000px) {
    .inventory__button-highlight:last-child {
        margin: 0 0 4px 0;
    }
}
@media screen and (max-width: 1138px) {
    .inventory__container {
        justify-content: center;
    }
}
