.soldier {
    display: flex;
    justify-content: start;
    align-items: center;
    width: 100%;
    max-width: 360px;
    height: 40px;
    padding: 4px 12px 4px 4px;
    border-radius: 6px;
    box-sizing: border-box;
    background-color: rgba(131, 128, 154, 0.2);
}

.soldier__img {
    width: 32px;
    height: 32px;
    margin: 0 12px 0 0;
    object-fit: contain;
}

.soldier__name {
    justify-self: start;
    flex-basis: 83%;
    font-size: 13px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.soldier__number {
    justify-self: flex-end;
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
