.time-line {
    width: 95%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: nowrap;
    box-sizing: border-box;
    padding: 20px 0;
    margin: 60px 0 0 0;
}
.time-line__item-count {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80px;
    padding: 20px;
    /*opacity: 0.7;*/
    box-shadow: inset 0 0 50px 30px rgba(0, 0, 0, 0.35);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(to bottom, rgba(0, 0, 0, 0.08) 0%, rgba(115, 115, 115, 0.29) 100%);
    border-image-slice: 1;
    background-origin: border-box;
    background-clip: content-box, border-box;
    box-sizing: border-box;
    border-radius: 10px;
}
.time-line__text {
    font-size: 30px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    background-clip: text;
    color: #fff;
}

.time-line__item-text {
    position: absolute;
    top: 30px;
    right: 10px;
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #b8b8b8;
}

.time-line__item-or {
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
    margin: 0 20px;
}
