.header__benefit {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90px;
    height: 25px;
    box-sizing: border-box;
}

.header__benefit-text {
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: rgba(255, 164, 0, 0.84);
    margin: 0;
    padding: 0;
}
