.header__user {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    max-width: 150px;
    margin: 0;
    padding: 0;
}

.header__user-avatar-container {
    width: 44px;
    height: 44px;
    margin: 0 8px 0 0;
    object-fit: contain;
    opacity: 90%;
    background-blend-mode: multiply;
    background-image: linear-gradient(to bottom, rgba(32, 49, 55, 0.46), rgba(32, 49, 55, 0.46));
}

.header__user-text-container {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    min-width: 90px;
    max-width: 130px;
}

.header__user-text {
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
    margin: 0;
    padding: 0;
    /*flex-basis: 300px;*/
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.header__user-text:last-child {
    color: #7e8487;
}
