.slider-classic {
    position: relative;
    width: 100%;
    height: 143px;
    gap: 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
}

.slider-classic__choice-container {
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    width: 100px;
    height: 104px;
    padding: 16px 14px;
    object-fit: contain;
    border-radius: 6px;
    box-shadow: inset 0 0 8px 2px #fff100;
    border-style: solid;
    border-width: 2px;
    border-image-source: linear-gradient(to bottom, #f3c216 1%, #ff8600 100%);
    border-image-slice: 1;
}
.slider-classic__choice-arrow {
    position: absolute;
    top: -20px;
    left: 0;
    right: 0;
    margin: auto;
}
