@import url('./vendor/font/manrope/manrope.css');
@import url('./vendor/normalize.css');

* {
    /*border: 1px solid red;*/
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    /*COLOR*/

    --text-gray-2: #b8b8b8;
    --text-dark: #605f68;
    --text-white: #f1f1f1;
    --blue: #00b5f4;
    --red: #ff0014;
    --text-gray-1: #7e8487;
    --black: #1c1c1c;
    --orange: #ffa400;
    --panels-ico-bg-active: rgba(0, 181, 244, 0.3);
    --text-blue: #2bc9ff;
    --greyish-purple-20: rgba(131, 128, 154, 0.2);
}

.body {
    background-color: #02020c;
    font-family: Manrope, 'Arial', sans-serif;
    -webkit-font-smoothing: subpixel-antialiased;
    -moz-osx-font-smoothing: grayscale;
    /*overflow-x: hidden;*/
}

.root {
    background-color: #02020c;
    min-width: 320px;
    width: 99.4vw;
    max-width: 1650px;
    margin: 0 auto;
    height: 100vh;
    max-height: 100%;
    /*overflow-x: hidden;*/
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
}

@media screen and (max-width: 769px) {
    .root {
        width: 100vw;
    }
}
