.progress-container {
    position: relative;
    display: flex;
    white-space: nowrap;
    padding: 0 0 0 35px;
    box-sizing: border-box;
    width: 100%;
}

.progress-levels {
    display: flex;
    justify-content: left;
    background-repeat: no-repeat;
    background-position: left;
    /*overflow-x: hidden;*/
    max-width: 100%;
    width: 100%;
}

.progress-level-block {
    display: flex;
    position: relative;
    /*width: 72px;*/
    /*height: 68px;*/
    min-width: 200px;
    width: 200px;
    height: 53px;
    background-image: url('../../../../images/progress-line.png');
    background-position: left;

    background-repeat: repeat-x;
    background-size: cover;
}
.progress-level-block__current-level {
    display: flex;
    position: absolute;
    top: -50px;
    left: 0;
    flex-direction: column;
    width: 100px;
    height: 52px;
    padding: 5px 12px 15px;
}
.progress-level-block__current-level_will-get {
    background-color: #1c1c1c;
}
.progress-level-block__current-level-text {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
}
.progress-level-block__current-level-text_point {
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #b8b8b8;
}
.progress-level-number {
    position: absolute;
    top: 12px;
    left: 22px;
    font-weight: bold;
    margin-bottom: 5px;
    font-size: 20px;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
    z-index: 10;
    /*background-image: url('../../../../images/badge.png');*/
}

/*.progress-bar {*/
