/*}*/

.progress-bar.filled {
    height: 12px;
    border-radius: 6px;
    box-shadow: inset -3px -3px 10px 0 rgba(0, 0, 0, 0.4);
    border: solid 1px #2bc9ff;
    z-index: 0;
    max-width: 100%;
}
.progress-bar.filled:first-child {
    background-image: url('../../../../images/svg/progress-line.svg');
    background-color: #2bc9ff;
}
.progress-bar__badge {
    position: absolute;
    left: -9px;
    top: -10px;
    width: 72px;
    height: 68px;
    background-image: url('../../../../images/badge.png');
    z-index: 9;
    background-size: cover;
    background-repeat: no-repeat;
}

.progress-level-xp {
    font-size: 12px;
    color: #666;
    margin-top: 5px;
}

.progress-bar-container {
    position: absolute;
    left: 60px;
    top: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}

.progress-bar-text {
    position: absolute;
    left: 150px;
    top: 120px;
    font-size: 16px;
    font-weight: bold;
    margin-left: 10px;
    color: #e5dede;
}

.progress-bar__button {
    position: absolute;
    left: 50px;
    top: 100px;
    margin-top: 10px;
    padding: 10px;
    z-index: 10;
}
