.header__balance {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 192px;
    height: 44px;
}

.header__balance-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 122px;
    height: 44px;
    margin: 0;
    padding: 0 8px;
    border-radius: 6px;
    background-color: rgba(131, 128, 154, 0.2);
    border: none;
    box-sizing: border-box;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    margin: 0;
    padding: 0;
    border-radius: 6px;
    background-color: rgba(131, 128, 154, 0.2);
    border: none;
}

.header__wallet {
}
.header__balance-text {
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #00b5f4;
    margin: 0;
    padding: 0;
}
