.ref-program {
    position: relative;
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(131, 128, 154, 0.15);
    margin-bottom: 20px;
}
.ref-program__button-details {
    position: absolute;
    right: 16px;
    top: 17px;
    background: none;
    border: none;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #7e8487;
    cursor: pointer;
    transition: 0.3s;
}
.ref-program__button-details:hover {
    scale: 1.1;
}
.ref-program__container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    box-sizing: border-box;
    padding: 16px;
    flex-basis: 600px;
}
.ref-program__container:last-child {
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    gap: 32px;
}
.ref-program__vertical-border {
    width: 1px;
    height: 150px;
    border-left: 1px solid rgba(255, 255, 255, 0.1);
}
.ref-program__title {
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #f1f1f1;
    margin: 0 0 24px 0;
}
.ref-program__ref-info {
    display: flex;
    justify-content: space-between;
    max-width: 526px;
    width: 100%;
    height: 44px;
    margin: 0 32px 16px 0;
    padding: 13px 12px 12px;
    border-radius: 6px;
    border: solid 1px #585a5c;
    background-color: #2c3136;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.ref-program__info-container {
    display: flex;
    gap: 7px;
    justify-content: space-between;
    margin: 0 0 20px;
}
.ref-program__info-block {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    justify-content: center;
    align-items: flex-start;
    width: 168px;
    height: 56px;
    padding: 9px 18px;
    gap: 3px;
    border-radius: 6px;
    background-color: rgba(131, 128, 154, 0.15);
}

.ref-program__block-title {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.ref-program__block-info {
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #f1f1f1;
}
.ref-program__block-bonus {
    display: flex;
    flex-direction: column;
    gap: 15px;
    width: 100%;
    max-width: 230px;
}
.ref-program__block-bonus-svg {
    display: flex;
    width: 100%;
    align-items: flex-start;
    justify-content: flex-start;
    gap: 7px;
}
.ref-program__block-bonus-text {
    font-size: 12px;
    font-weight: 500;
    flex-basis: 100px;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.ref-program__block-bonus-result {
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #f1f1f1;
}
