.create-rooms {
    position: absolute;
    top: 65px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    width: 400px;
    height: 152px;
    border-radius: 6px;
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    background-color: rgba(30, 34, 37, 0.95);
    box-sizing: border-box;
    border: none;
    z-index: 50;
}
.create-rooms__button {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 120px;
    height: 120px;
    padding: 10px;
    border-radius: 6px;
    box-sizing: border-box;
    border: none;
    background-color: rgba(30, 34, 37, 0.95);
}

.create-rooms__button:hover {
    background-color: #353535;
}
.create-rooms__text {
    white-space: nowrap;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #b8b8b8;
}
