.classic {
    display: flex;
    justify-items: start;
    flex-direction: column;
    width: 100%;
    border-radius: 6px;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.classic__background-img {
    position: absolute;
    top: -13px;
    left: 0;
    width: 120px;
    height: 120px;
    object-fit: contain;
}
.classic__background-text {
    position: absolute;
    top: 29px;
    left: 24px;
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.classic__secure-container {
    position: absolute;
    top: 16px;
    right: 16px;
    gap: 8px;
    display: flex;
    justify-content: space-between;
    width: 100px;
}
.classic__up-container {
    display: flex;
    justify-content: center;
    gap: 5%;
    align-items: center;
    min-height: 430px;
    width: 100%;
    box-sizing: border-box;
    padding: 7% 0 3% 5%;
}

.classic__down-container {
    display: flex;
    justify-content: center;
    gap: 24px;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
}
