.button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 52px;
    height: 44px;
    margin: 0;
    padding: 0;
    border-radius: 6px;
    background-color: rgba(131, 128, 154, 0.2);
    border: none;
}

.button__text {
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
}
.button_background {
    background-image: linear-gradient(to bottom, rgba(32, 49, 55, 0.46), rgba(32, 49, 55, 0.46));
}
