.block-side-bar-skin {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    max-width: 280px;
    height: 207px;
    background-image: url('../../../images/BlockSkin.webp');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    box-sizing: border-box;
}
.block-side-bar-skin__title {
    position: absolute;
    top: 26px;
    background-image: linear-gradient(to bottom, #959595 13%, #fff 54%, #e5dede 55%, #d8d8d8 89%);
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.block-side-bar-skin__img {
    width: 140px;
    height: 86px;
    /*margin: 32px 0 20px;*/
    object-fit: cover;
    /*box-shadow: 0 2px 24px 0 rgba(0, 0, 0, 0.5);*/
}

.block-side-bar-skin__button {
    position: absolute;
    bottom: 24px;
    width: 114px;
    height: 40px;
    border-radius: 10px;
    border: none;
    -webkit-backdrop-filter: blur(2px);
    backdrop-filter: blur(2px);
    background-color: rgba(16, 25, 29, 0.5);
    z-index: 1;
}

.block-side-bar-skin__text {
    width: 60px;
    height: 19px;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #f1f1f1;
}
