.rooms {
    position: relative;
    width: 100%;
    /*height: 50vh;*/
    max-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
    gap: 5px;
    padding: 0 20px;
}
.rooms__background-img {
    position: absolute;
    left: 0;
    width: 120px;
    height: 120px;
    object-fit: contain;
}
.rooms__background-text-container {
    position: absolute;
    display: flex;
    gap: 6px;
    flex-direction: column;
    top: 29px;
    left: 24px;
}
.rooms__background-text {
    /*top: 29px;*/
    /*left: 24px;*/
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
}
.rooms__secure-container {
    position: absolute;
    top: 16px;
    right: 16px;
    gap: 8px;
    display: flex;
    justify-content: space-between;
}
.rooms__button-change {
    position: relative;
    width: 200px;
    height: 56px;
    padding: 15px 0 14px;
    border-radius: 10px;
    box-shadow: inset 0 0 15px 0 rgba(255, 140, 0, 0.69);
    border-style: solid;
    border-width: 1px;
    margin: 0 15px 0 0;
    border-image-source: linear-gradient(to bottom, #ff7474, rgba(255, 145, 145, 0.3));
    border-image-slice: 1;
    background-image: radial-gradient(circle at 50% 84%, #f00, rgba(255, 0, 0, 0.25) 47%),
        linear-gradient(to bottom, rgba(255, 0, 0, 0.48), rgba(255, 0, 0, 0.5)), linear-gradient(to bottom, #ff7474, rgba(255, 145, 145, 0.3));
    background-origin: border-box;
    background-clip: content-box, content-box, border-box;
}
.rooms__settings-container {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 100px 0 24px 0;
}
.rooms__settings-text {
    width: 12%;
    min-width: 185px;
    display: flex;
    text-align: center;
    align-items: center;
    flex-wrap: nowrap;
    white-space: nowrap;
    justify-content: space-between;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
    margin: 3px;
}
.rooms__settings-form {
    display: flex;
    justify-content: flex-start;
    width: 60%;
}
.rooms__button-change {
}
.rooms__accordion-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    gap: 5px;
    margin-top: 10px;
}
