.online {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 90px;
    height: 30px;
    margin: 0;
    padding: 0;
}

.online__icon {
    position: absolute;
    top: 1px;
    right: 0;
    width: 16px;
    height: 16px;
}

.online__text {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
    margin: 0;
    padding: 0;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
