.block-list {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    width: 50%;
    max-width: 378px;
    min-height: 190px;
    gap: 4px;
    margin: 0;
    padding: 61px 12px 7px 16px;
    border-radius: 10px;
    box-sizing: border-box;
    background-image: linear-gradient(to bottom, rgba(49, 64, 68, 0.7), #161616);
}

.block-list__header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 48px;

    background-image: url('../../../images/BlockListBlack.png');
    background-position: top;
    background-repeat: no-repeat;
    background-size: contain;
}
.block-list__title {
    position: absolute;
    top: 15px;
    left: 16px;
    max-width: 120px;
    height: 19px;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
    margin: 0;
    padding: 0;
}

.block-list__combined-shape {
    position: absolute;
    top: -3px;
    left: 0;
    right: 10px;
    margin: auto;
    width: 40px;
    height: 26px;
    font-size: 48px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.block-list__leadership-svg {
    position: absolute;
    top: 16px;
    right: 98px;
}
.block-list__leadership {
    position: absolute;
    top: 16px;
    right: 78px;
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #b8b8b8;
}
.block-list__add-user {
    position: absolute;
    top: 15px;
    right: 20px;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #b8b8b8;
}
