.accordion {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 128px;
    padding: 16px 0 16px 16px;
    border-radius: 6px;
    background-color: rgba(73, 68, 77, 0.52);
    box-sizing: border-box;
    cursor: grab;
}
.accordion_active {
    background-color: rgba(104, 106, 114, 0.63);
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
}
.accordion__users-container {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.accordion__user {
    /*position: relative;*/
    display: flex;
    /*max-height: 100px;*/
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100px;
}
.accordion__user-text {
    /*position: absolute;*/
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 20px;
    object-fit: contain;
    border-radius: 6px;
    background-color: rgba(23, 22, 26, 0.5);
    box-sizing: border-box;
    font-size: 11px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #b8b8b8;
}
.accordion__add-user {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 80px;
    margin: 0 0 0 8px;
    padding: 28px 18px;
    object-fit: contain;
    border-radius: 6px;
    border: dashed 1px #605f68;
    background-color: rgba(31, 34, 37, 0.4);
    cursor: pointer;
    z-index: 3;
}

.accordion__container-skin {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 4px;
}
.accordion__img {
    width: 60px;
    height: 60px;
}

.accordion__money {
    position: absolute;
    right: 30px;
    top: 30px;
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: rgba(255, 255, 255, 0.87);
}
.accordion__line-text {
    position: absolute;
    right: 105px;
    top: 10px;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #7e8487;
    white-space: nowrap;
}
.accordion__line-text_my {
    right: 16px;
    top: 89px;
}
.accordion__line-text-money {
    /*position: absolute;*/
    /*right: 0;*/
    /*top: 40px;*/
    color: #b8b8b8;
    font-size: 14px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    margin: 0 5px 0;
}
.accordion__background {
    position: absolute;
    right: 0;
    top: 0;
    width: 172px;
    height: 64px;
}
