.layout-horizontal {
    display: flex;
    justify-items: start;
    align-items: start;
    max-width: 2000px;
    max-height: 100%;
    width: 100%;
}

.layout-horizontal__main {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-items: start;
    width: 100%;
    /*max-width: 1400px;*/
    min-height: 60%;
    max-height: 100%;
    overflow-y: scroll;
    border-radius: 6px;
    background-image: linear-gradient(to bottom, rgba(49, 64, 68, 0.7), rgba(22, 22, 22, 0.7) 99%);
    margin: 0;
    padding: 0;
    -webkit-transition: background 1s;
    transition: background 1s;
}
.layout-horizontal__main::-webkit-scrollbar {
    width: 5px;
    height: 10px;
}
.layout-horizontal__main::-webkit-scrollbar-track {
    background: rgba(255, 165, 0, 0); /* цвет дорожки */
}
.layout-horizontal__main::-webkit-scrollbar-thumb {
    background-color: rgba(104, 106, 114, 0); /* цвет плашки */
    border-radius: 10px; /* закругления плашки */
    border: 1px solid rgba(104, 106, 114, 0); /* padding вокруг плашки */
    transition: background-color 0.8s;
}
.layout-horizontal__main:hover::-webkit-scrollbar-thumb {
    background-color: rgba(104, 106, 114, 0.4); /* цвет плашки */
    border: 1px solid rgba(104, 106, 114, 0.19); /* padding вокруг плашки */
}
