.line-skin {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 100px;
    width: 100%;
    height: 84px;
    padding: 16px 0 16px 16px;
    border-radius: 6px;
    background-color: rgba(131, 128, 154, 0.18);
}
.line-skin__container-skin {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 4px;
}
.line-skin__img {
    width: 60px;
    height: 60px;
}

.line-skin__money {
    position: absolute;
    right: 16px;
    top: 10px;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #f1f1f1;
}
.line-skin__ticket {
    position: absolute;
    right: 16px;
    top: 51px;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #7e8487;
}
.line-skin__background {
    position: absolute;
    right: 0;
    top: 0;
    width: 108px;
    height: 40px;
}
