.title {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url('../../images/title.webp');
    background-position: center;
    background-repeat: no-repeat;
}

.title_position_center {
    width: 200px;
    height: 40px;
    margin: 0 auto;
    box-sizing: border-box;
    background-size: contain;
}

.title_position_left {
    width: 240px;
    height: 51px;
    background-size: cover;
    margin-left: -30px;
}
