.title__text {
    text-align: center;
    /*width: 51px;*/
    /*height: 27px;*/
    background-image: linear-gradient(to bottom, #959595 13%, #fff 54%, #e5dede 55%, #d8d8d8 89%);
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0;
    padding: 0;
}

.title__text_position_left {
    width: 144px;
}
