.game__skins {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    box-sizing: border-box;
}
.game__skins-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    background-image: radial-gradient(circle at 50% 129%, rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.08) 27%);
    box-sizing: border-box;
    padding: 60px 20px 20px;
}
.game__skins-chance-container {
    width: 100%;
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    gap: 8px;
}
.game__skins-avatar-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.game__skins-avatar-chance {
    text-align: center;
    width: 60px;
    height: 20px;
    object-fit: contain;
    border-radius: 6px;
    background-color: rgba(23, 22, 26, 0.5);
    font-size: 11px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
    padding: 3px 0 0 0;
}

.game__skins-avatar-team-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 110px;
    gap: 8px;
    bottom: -5px;
    padding: 10px 4px 5px;
    object-fit: contain;
    border-radius: 6px;
    background-image: linear-gradient(to bottom, rgba(0, 206, 241, 0.5), rgba(0, 206, 241, 0.1) 99%);
}

.game__skins-avatar-team-text {
    position: absolute;
    display: flex;
    white-space: nowrap;
    text-align: center;
    top: 2px;
    left: 1px;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #f1f1f1;
}
.game__skins-down-container {
    display: flex;
    flex-direction: column;
    border-radius: 6px;
    background-image: radial-gradient(circle at 50% 129%, rgba(133, 133, 133, 0.56), rgba(255, 255, 255, 0.08) 29%);
    width: 100%;
    height: 100%;
    gap: 4px;
    max-height: 300px;
    box-sizing: border-box;
    padding: 12px 12px 87px;
    margin: 15px 0 0 0;
}
