.classic__final {
    position: relative;
    width: 100%;
    height: 100vh;
    max-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    background-image: url('../../../images/winclassicimg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
.classic__final-up-container {
    width: 100%;
    display: flex;
    min-height: 50%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.classic__final-user-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.classic__final-user-background {
    width: 182px;
    height: 162px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url('../../../images/win-bg-cl.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    margin: 0 0 24px 0;
}
.classic__final-user-name {
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #2bc9ff;
    margin: 0 0 2px 0;
}
.classic__final-user-victory {
    font-size: 20px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f1f1f1;
    margin: 0 0 15px 0;
}
.classic__final-team-text {
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.classic__final-down-container {
    display: flex;
    width: 60%;
    height: 35%;
    justify-content: space-between;
    align-items: flex-start;
    box-sizing: border-box;
    padding: 40px 0 0 0;
}
.classic__final-ticket {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    max-height: 90px;
}
.classic__final-ticket-text {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #b8b8b8;
}
.classic__final-ticket-percent {
    font-size: 40px;
    font-weight: 800;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f1f1f1;
}

.classic__final-ticket-id-text {
    color: #7e8487;
}
.classic__final-border {
    width: 97%;
    height: 1px;
    background-color: rgba(255, 255, 255, 0.1);
}
