.header-navigation {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    width: 30%;
    min-width: 420px;
}

.header-navigation__links {
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.22;
    letter-spacing: normal;
    color: rgba(184, 184, 184, 0.78);
    text-decoration: none;
    margin: 0;
    padding: 0;
}
