.accordion-container {
    position: relative;
    top: -5px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    background-color: #83809a26;
    padding: 24px 20px 0;
}
.accordion-container__container-wait {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 80px;
    box-sizing: border-box;
    padding: 0 20px;
    margin: 0 0 24px 0;
    background-color: rgba(126, 132, 135, 0);
    border-radius: 6px;
    background-image: linear-gradient(to right, rgba(255, 255, 255, 0.15) 0%, rgba(255, 255, 255, 0.05) 100%);
}
.accordion-container__container-wait-background-count {
    position: absolute;
}
.accordion-container__container-wait-text {
    font-size: 13px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #b8b8b8;
}
.accordion-container__container-wait-text_time {
    position: absolute;
    right: 18px;
}
.accordion-container__container-wait-text-timer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-shadow: 0 0 2px #fff;
    background-image: linear-gradient(to bottom, #959595 13%, #fff 54%, #e5dede 55%, #d8d8d8 89%);
    font-size: 30px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}
.accordion-container__container-wait-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0 24px 0 20px;
    width: 190px;
    height: 44px;
    border-radius: 10px;
    box-shadow: inset 0 0 15px 0 rgba(0, 181, 244, 0.69);
    border-style: solid;
    border-width: 1px;
    border-image-source: linear-gradient(to bottom, #00b5f4, rgba(0, 181, 244, 0.3) 99%);
    border-image-slice: 1;
    background-image: radial-gradient(circle at 50% 100%, #00b5f4, rgba(0, 181, 244, 0.25) 42%), linear-gradient(to bottom, #003a4e, #003a4e),
        linear-gradient(to bottom, #00b5f4, rgba(0, 181, 244, 0.3) 99%);
    background-origin: border-box;
    background-clip: content-box, content-box, border-box;
    font-size: 14px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #f1f1f1;
}
.accordion-container__container-skins {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-height: 30%;
    box-sizing: border-box;
    height: 80px;
    padding: 18px 17px;
    /*border: 1px solid red;*/
    box-sizing: border-box;
}

.accordion-container__skins {
    display: flex;
    justify-content: center;
    gap: 4px;
    align-items: center;
}
.accordion-container__count-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-between;
    gap: 3px;
}
.accordion-container__count-money {
    font-size: 13px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #f1f1f1;
}
.accordion-container__count-skins {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: right;
    color: #7e8487;
}

.accordion-container__footer-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px 0;
}
.accordion-container__game-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.accordion-container__hash-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.accordion-container__footer-text {
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #7e8487;
    white-space: nowrap;
}
.accordion-container__footer-text:nth-child(2) {
    margin: 0 0 0 5px;
}
